import React, { Component } from "react";
import {
  Divider,
  Grid,
  Image,
  Table,
  Segment,
  Visibility
} from "semantic-ui-react";

export default class VisibilityComp extends Component {
  state = {
    calculations: {
      direction: "none",
      height: 0,
      width: 0,
      topPassed: false,
      bottomPassed: false,
      pixelsPassed: 0,
      percentagePassed: 0,
      topVisible: false,
      bottomVisible: false,
      fits: false,
      passing: false,
      onScreen: false,
      offScreen: false
    }
  };

  handleOnScreen = (e, { calculations }) => this.setState({ calculations });

  handleOffScreen = (e, { calculations }) => this.setState({ calculations });

  render() {
    const { calculations } = this.state;

    return (
      <Grid>
        <Grid.Column>
          <Visibility
            fireOnMount
            onOnScreen={this.handleOnScreen}
            onOffScreen={this.handleOffScreen}
          >
            <Segment>
              <Image src="https://react.semantic-ui.com/images/wireframe/centered-paragraph.png" />
              <Divider />
              <Image src="https://react.semantic-ui.com/images/wireframe/short-paragraph.png" />
              <Divider />
              <Image src="https://react.semantic-ui.com/images/wireframe/media-paragraph.png" />
              <Divider />
              <Image src="https://react.semantic-ui.com/images/wireframe/paragraph.png" />
            </Segment>
          </Visibility>
        </Grid.Column>
      </Grid>
    );
  }
}



